.terminal {
  padding: 1em 1.5em;
  font-size: .8em;
  font-family: Menlo, 'Roboto Mono', monospace;
  border: 1px solid #d8d9da;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, .2);
  max-width: 100%;
  border-radius: 4px;
  margin: 30px 0;
}

code[class*="language-"],
pre[class*="language-"],
pre code {
  color: $code-font-color;
  font-family: Menlo, 'Roboto Mono', monospace;
  direction: ltr;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  font-size: .9em;
  line-height: 1.3;
  font-weight: 400;
  -moz-tab-size: 2;
  -o-tab-size: 2;
  tab-size: 2;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
  -webkit-font-smoothing: antialiased;
}

pre code {
  display: block;
}

@media screen and (min-width: 960px) {
  pre code {
    padding-left: calc(50% - 400px);
  }
}


/* Code blocks */

pre code {
  padding: 1.5em;
  margin: .5em 0;
  overflow: auto;
  border: 0px solid $code-background;
  background-color: $code-background;
  font-size: .8em;
}

:not(pre) > code[class*="language-"],
pre[class*="language-"] {}


/* Inline code */

fdfd:not(pre) > code[class*="language-"] {
  padding: .2em;
  padding-top: 1px;
  padding-bottom: 1px;
  background: darken($code-background, 2%);
  border: 0px solid #ececec;
  -webkit-font-smoothing: antialiased;
}

:not(pre) > code {
  font-size: .9em;
  padding: .2em;
  padding-top: 1px;
  padding-bottom: 1px;
  background: darken($code-background, 2%);
  font-family: Menlo, 'Roboto Mono', monospace;
  color: $code-font-color;
  font-weight: 400;
  // -webkit-font-smoothing: subpixel-antialiased;
}

$string: #36966A;
code[class*=language-css],
code[class*=language-scss],
code[class*=language-less],
code[class*=language-sass] {
  color: #795da3;
}

.token.doctype {
  background: #000;
  color: #fff;
}

.token.comment,
.token.prolog,
.token.cdata {
  color: #787878; //font-style: italic;
}

.token.namespace {
  opacity: .7;
}

.token.string,
.token.attr-value {
  color: $string;
}

.token.punctuation {
  color: #454545;
}

.token.operator {
  color: RGBA(83, 34, 132, 1);
  /* no highlight */
}

.token.variable {
  color: #183691;
}

.token.property {
  color: #4f545a;
}

.token.entity,
.token.url,
.token.symbol,
.token.number,
.token.boolean,
.token.constant,
.token.regex,
.token.inserted {
  color: #A85479;
}

.token.delimiter {
  background: #dedede;
  color: #333;
  padding: 1px 2px;
}

.token.atrule {
  color: #183691;
}

.token.keyword,
.language-autohotkey .token.selector {
  color: #A85479;
}

.token.attr-name {
  color: #795da3;
}

.token.function {
  color: #287BB6;
}

.token.class-name {
  color: #795da3;
}

.token.deleted,
.language-autohotkey .token.tag {
  color: #f2777a;
}

.token.tag,
.language-autohotkey .token.keyword {
  color: #287BB6;
}

.token.selector {
  color: #287BB6;
}

.token.italic {
  font-style: italic;
}

.language-html .token.punctuation {
  color: #454545;
}