/* Main Styling */

*,
*:before,
*:after {
	box-sizing: border-box;
}
html {
	-webkit-font-smoothing: antialiased;
}
body {
	// font: italic small-caps normal 13px/150% Arial, Helvetica, sans-serif;
	font: normal normal 400 #{$font-size}/#{1.4} $font-family;
	color: $font-color;
}
@media screen and (min-width: $mobile-breakpoint) {
	body {
		// font: italic small-caps normal 13px/150% Arial, Helvetica, sans-serif;
		font: normal normal 400 #{$font-size-desktop}/#{1.4} $font-family;
		color: $font-color;
	}
}

p, h1, h2, h3, h4, h5, div, ul, li, ol, blockquote, .alert, .info, .terminal, .article-excerpt, .first, .post-links {
  margin-left: auto;
  margin-right: auto;
  max-width: $content-small-width;
}

p {
	margin-top: 1.3em;
  margin-bottom: 1.3em;
}
h1,
h2,
h3,
h4,
h5 {
	font-family: $heading-font-family;
	color: $heading-color;
	font-weight: 800;
	line-height: 1.2;
	letter-spacing: -.05em;
}
h1 {
	font-size: 2em;
	margin: 0;
}
h2 {
	font-size: 1.4em;
	margin: 10px 0 0;
	font-family: $article-title;
	letter-spacing: -.05em;
}
a:hover h2 {
	color: #000;
}
h3 {
	font-size: 1.3em;
	letter-spacing: -.05em;
}
h4 {
	font-size: 1.2em;
	font-weight: 600;
	color: $sub-font-color;
}
h5 {
	color: $font-color;
	font-weight: 600;
	font-size: 1.1em;
	margin: 20px 0 0;
}
@media screen and (min-width: $mobile-breakpoint) {
	h2 {
		font-size: 1.8em;
	}
	h3 {
		font-size: 1.6em;
	}
}
header {
	padding: 5px 5px 0;
	background: #ffffff;
  margin-bottom: 3em;
}
.logo-image {
	max-width: 50px;
	margin: 0;
	box-shadow: none;
}
.container {
	max-width: $content-width;
	margin: 0 auto;
	padding: 1em;
}
.large-container {
	max-width: $content-width;
}
article {
	padding: 1.5em 0;
	border-bottom: 1px solid $slab;
}
@media screen and (min-width: $mobile-breakpoint) {
	.container {
		padding: 1em 2.5em;
	}
	article {
	}
	article.single-article {
		h2 {
			font-size: 2.4em;
		}
	}
}
.article-excerpt { 
	color: $sub-font-color;
	&:hover {
		color: $font-color;
		background: rgba(0, 5, 15, .04);
	}
	.first {
		color: $font-color;
	}
}
@media screen and (min-width: $mobile-breakpoint) {
	.article-excerpt {
		padding-left: 1em;
		padding-right: 1em;
	}
}
a {
	color: $link-color;
	text-decoration: none;
	&:visited,
	&:active,
	&:focus {
		color: $link-color;
	}
	&:hover {
		color: $link-hover-color;
	}
}
h2 {
	& a,
	& a:visited {
		color: $heading-color;
	}
	& a:hover {
		color: #000;
	}
}
p,
span,
li {
	& a {
		&:hover {
			text-decoration: underline;
		}
	}
}
blockquote {
	margin: 20px 0;
	color: #4f545a;
	padding: 2px 0 0 25px;
	border-left: 12px solid $code-background;
	font-family: $heading-font-family;
	& p {
		margin: 0;
		font-weight: 700;
	}
	& cite {
		display: block;
		margin-top: 25px;
		font-size: .9em;
		text-align: right;
	}
	&.info {
		background: #FFF4CA;
		border: 1px solid #FFE168;
		border-radius: 4px;
		color: #755D00;
		padding: 15px;
		p {
			font-weight: 400;
			margin-bottom: 1em;
			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}
	&.note {
		background: $code-background;
		border: 1px solid $border;
		border-radius: 4px;
		color: $heading-color;
		padding: 15px;
		p {
			font-weight: 400;
			margin-bottom: 1em;
			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}
}
.alert {
	transition: all .3s ease;
	color: $heading-color;
	border: 1px solid $border;
	border-radius: 4px;
	background: $code-background;
	padding: 20px;
	font-family: $heading-font-family;
	margin: 20px 0;
	a,
	a:visited {
		color: #536172;
		font-weight: 600;
	}
	&:hover {
		color: #576778;
		border: 1px solid #576778;
		box-shadow: 0 1px 1px 0 rgba(0, 0, 0, .1);
	}
}
cite,
mark,
strong,
label {
	font-family: $heading-font-family;
}
hr {
	border: 0;
	border-top: 1px solid $slab;
	margin: 50px 0;
}
time {
	color: $sub-font-color;
	font-family: $button;
	font-size: .8em;
	margin-bottom: 30px;
	font-weight: 600;
	text-transform: uppercase;
	padding-left: 5px;
}
mark {
	padding: 8px 15px;
	background: $link-color;
	margin: 10px 0 0;
	color: #fff;
	border: 1px solid darken($link-color, 20%);
	font-weight: 700;
	display: inline-block;
}
.yellow {
	background: #FFEB54;
	border: 1px solid darken(#FFEB54, 20%);
	color: #262626;
}
.green {
	background: #0A9C5E;
	border: 1px solid darken(#0A9C5E, 20%);
}
dt {
	font-weight: 700;
	margin-top: 20px;
	font-family: $heading-font-family;
	font-size: 1.1em;
}
.bar {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.pad {
	padding: 15px;
}
.center {
	text-align: center;
}
.article-header {
	margin-bottom: 10px;
}
.posts-links {
	padding: 0 15px;
	margin: 40px 0;
	&:after {
		content: '';
		display: table;
		clear: both;
	}
}
@media screen and (min-width: $mobile-breakpoint) {
	.article-header {
		margin-bottom: 30px;
	}
}
footer {
	padding: 20px;
	text-align: center;
	font-size: .8em;
	h1 a {
		color: lighten($font-color, 15%);
		&:visited,
		&:active,
		&:focus {
			color: lighten($font-color, 15%);
		}
	}
	h1 a:hover {
		color: #576778;
	}
}
.social {
	text-align: right;
}
.darken {
	transition: all .2s ease;
	opacity: .5;
	margin: 0;
	box-shadow: none;
	&:hover {
		opacity: .9;
	}
}
.vertical {
	align-items: center;
}
.twitter-button {
	max-width: 80px;
}
.no-left-border {
	border-left: 0 !important;
}
.break {
	padding-bottom: 20px;
}
.right-text {
	text-align: right;
}
.social-bar {
	font-size: 1px;
	.fb-share-button {
		margin-left: 10px;
	}
}
.block {
	display: block;
	text-align: center;
}
.block-i {
	vertical-align: middle;
	display: inline-block;
	float: left;
}
img {
	max-width: 100%;
}
.left,
.right {
	float: none;
	padding: 5px;
	margin: 0 auto;
	text-align: center;
}
.clear {
	clear: both;
}
.image-float {
	max-width: 50px;
	float: left;
	margin-right: 15px;
}
@media screen and (min-width: $content-small-width) {
	.left {
		float: left;
	}
	.right {
		float: right;
	}
	.image-float {
		max-width: 100px;
		padding-right: 20px;
	}
}
.article-image {
	display: block;
	margin: 0 auto;
	padding-bottom: 15px;
}
::selection {
	background: rgba(0, 0, 0, .1);
}
